@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');

html, body {
    margin: 0;
    padding: 0;
    font-size: 13pt;
    font-family: 'Oswald', sans-serif;
    height: 100%;
}

.MainForm {
    padding: 10pt 100pt;
    margin: 0;
    align-content: center;
}

.headerStyle {
    width: 100%;
    height: auto;
    padding: 30pt;
    align-items: center;
    text-align: center;
    margin: 0;
}

.form-group {
    padding: 10px;
    margin-bottom: 2rem;
    background: rgba(192, 192, 192, 0.7);
    border-radius: .25rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.loader {
    border: 5px solid #f3f3f3; /* Light grey */
    border-top: 5px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 20px;
    height: 20px;
    display: inline-block;
    animation: spin 2s linear infinite;
    margin: 0;
    vertical-align: middle;
}

.container {
    margin: 0;
}

.loaderContainer {
    margin: 0;
    width: 40px;
    display: inline-block;
    height: 100%;
    vertical-align: middle;
}

.buttonText {
    width: 100%;
    height: 100%;
}

#enviar {
    height: 50px;
    width: 150pt;
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@media (min-width: 1000px) {
    .App {
        padding: 20pt;
        height: 100vh;
    }

    .left {
        float: left;
        display: table;
        /*background-image: linear-gradient(to right,rgba(187, 187, 187, 0.486), rgba(255, 255, 255, 0));*/
        background-image: url("./images/foulard_fons-1.png"), linear-gradient(to right,rgba(187, 187, 187, 0.486), rgba(187, 187, 187, 0));
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        width: 30%;
        height: 100%;
        overflow: auto;
        box-sizing: border-box;
        padding: 0.5em;
        border-radius: 20px 0 0 20px;
    }

    .headerStyle {
        display: table-cell;
        vertical-align: middle;
    }

    .mainTitle {
        color: black;
        font-size: 40pt;
        text-shadow: 2px 2px 8px grey;
    }

    .MainForm {
        float: left;
        width: 70%;
        height: 100vh;
        max-height: 100%;
        overflow: auto;
        box-sizing: border-box;
        padding: 0.5em;
    }
}

@media only screen and (max-width: 660px) {
    .MainForm {
        padding: 10%;
    }

    #enviar {
        width: 100%;
    }
}
  